.nav {
    grid-area: n;
    background-color:#ffc290;
    padding-left: 10px;
    padding-top: 10px;
    
  }

  .item :any-link{
      color:#0040ff;
      text-decoration: none;
  }
  .item a.active {
      color: #054705;
  }
  .friends{
      margin-top: 100px;
      border: 2px double white ;
      width: 170px;
      text-align: center;
      height: auto;
  }


  .friendEl img{
      width: 20px;
      height: 20px;
      border-radius: 30px;
      border: yellowgreen;

  }
  .friendEl :any-link  {
      display: inline-block;
      justify-items: left;
      text-align: center;
      margin-right: 5px;
      text-decoration: none;
      color: darkblue;
      font-size: x-small;
  }
