.app-wrapper{
  margin: 0 auto;
height: 1020px;
  width: 1200px;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
  "h h"
  "n c"
  ;
  font-size: larger;
font-family: Georgia;
}
.app-wrapper-content{
  grid-area: c;
  background-color: #00264d;

}
